<template>
    <div class="w-full h-full flex flex-col justify-evenly items-center bg-[rgb(230,230,180)] pl-4 pr-4 font-bold">
        <template v-if="warningType === 'in tencent app'">
            <div v-for="(line, index) in warningInTencentApp" :key="index">{{ line }}</div>
        </template>
        <template v-else-if="warningType === 'blob cannot download'">
            <div v-for="(line, index) in warningBlobCannotDownload" :key="index">{{ line }}</div>
        </template>
    </div>
</template>
<script setup>
import { defineProps } from 'vue'
defineProps({
    warningType: {
        type: String,
        required: true,
    },
})
const warningBlobCannotDownload = [
    'Blob',
    'CGCBlobURL',
    'ChromeFirefoxEdge',
    'Blob',
]

const warningInTencentApp = [
    'QQCGC',
    'QQ',
    'CGCQQ',
    'ChromeFirefoxEdge',
    'QQ',
    '',
]

</script>
