const getAllUpdates = (pitch) => {
    const updates = []
    const currentLog = updateLog[pitch]
    updates.push(currentLog)
    if (!currentLog.lastPitch) { return updates }
    const previousUpdates = getAllUpdates(currentLog.lastPitch)
    previousUpdates.forEach(update => {
        updates.push(update)
    })
    return updates
}

export { getAllUpdates }