const getAbilityInitialValue = (label) => {
    if (!(abilityData[label])) { return 0 }
    return abilityData[label][0]
}

const getAbilityComprehensionCost = (label) => {
    if (!(abilityData[label])) { return 0 }
    return abilityData[label][2]
}

const getAbilityPercentage = (label, dimension) => {
    const getDimensionIndex = (dimension) => {
        switch (dimension) {
            case '':
                return 0
            case '':
                return 1
            case '':
                return 2
            case '':
                return 3
            case '':
                return 4
            case '':
                return 5
            case '':
                return 6
            case '':
                return 7
            case '':
                return 8
        }
    }
    if (!(abilityData[label])) { return null }
    return abilityData[label][1][getDimensionIndex(dimension)]
}

export { abilityData, getAbilityInitialValue, getAbilityComprehensionCost, getAbilityPercentage }