import { defineStore } from 'pinia'
import { getVersionInitialisation } from '@/utils/stateInitialiser'
import { abilityData, getAbilityPercentage, getAbilityComprehensionCost } from '@/data/abilityData'
import { raceInstructions } from '@/data/instructions'

export const useWebStatusStore = defineStore('web_status', {
    state: () => ({
        currentCharacter: null,
        mouseTip: false,
        mouseTipContent: [],
        windowAlert: false,
        windowAlertTitle: '',
        windowAlertContent: [],
    }),
    getters: {
        getPoints: (state) => (label) => {
            let value = (state.currentCharacter)[label][0]
            if (!Number.isInteger(value)) { value = 0 }
            if (value < 0) { value = 0 }
            if (value <= 600) return value
            if (value <= 800) return value * 1.5 - 300
            if (value <= 1000) return value * 2 - 700
            return value * 3 - 1700
        },
        statePoints: (state) => {
            let pts = 0
            const allStates = ['', '', '', '', '', '', '', '']
            allStates.forEach(label => {
                pts += state.getPoints(label)
            })
            return pts
        },
        maxStatePoints: (state) => {
            if ((state.currentCharacter)[''] === '') { return 0 }
            else if ((state.currentCharacter)[''] === '') { return 4800 + (state.currentCharacter)[''] }
            else { return raceInstructions[(state.currentCharacter)['']][''] + (state.currentCharacter)[''] }
        },
        generalStatePoint: (state) => {
            let pts = 0
            const allStates = ['', '', '', '', '', '', '', '']
            allStates.forEach(label => {
                pts += (state.abilityPoints(label) >= state.usedStateAbilityPoints(label) ? state.abilityPoints(label) - state.usedStateAbilityPoints(label) : 0)
            })
            pts += state.currentCharacter[``]
            return pts
        },
        abilityPoints: (state) => (label) => {
            const value = state.currentCharacter[label][0] * 2 + state.currentCharacter[label][0] * state.level * 0.05
                + state.level * 0.5 + state.currentCharacter[`${label}`]
            return Math.floor((value).toFixed(3))
        },
        comprehensionPoints: (state) => {
            let pts = 0
            Object.keys(abilityData).forEach(key => {
                pts += (state.currentCharacter)[key][0] !== 0 ? getAbilityComprehensionCost(key) : 0
            });
            (state.currentCharacter)[''].forEach(talent => {
                pts += talent[2]
            })
            return pts
        },
        usedStateAbilityPoints: (state) => (label) => {
            let pts = 0
            Object.keys(abilityData).forEach(key => {
                pts += Math.ceil(state.getPoints(key) * getAbilityPercentage(key, label))
                if (getAbilityPercentage(key, label) === null) { console.warn(`${key}${label}`) }
            })
            return pts
        },
        level: (state) => {
            if ((state.currentCharacter)[''] < 0) return 0
            const base = (state.currentCharacter)[''] * 0.2 + 0.25
            const result = Math.floor((Math.pow(base, 0.5) - 0.5).toFixed(3))
            return result > 500 ? 500 : result
        },
        health: (state) => {
            return Math.floor((state.currentCharacter)[''][0] * 2 + 500 + Math.pow(state.level, 0.75) * 10)
        },
        mana: (state) => {
            return Math.floor((state.currentCharacter)[''][0] * 0.2 + state.level * 0.5)
        },
        damage: (state) => {
            return (state.currentCharacter)[''][0] * 0.12 + 40
        },
        attack: (state) => {
            return Math.max((state.currentCharacter)[''][0] * 0.08 + (state.currentCharacter)[''][0] * 0.02 + state.level * 2, 40)
        },
        defend: (state) => {
            return Math.max((state.currentCharacter)[''][0] * 0.1 + state.level * 2, 40)
        },
        speed: (state) => {
            return Math.floor(Math.pow((state.currentCharacter)[''][0], 0.9) * Math.pow((state.currentCharacter)[''][0], 0.1))
        },
        morale: (state) => {
            return Math.floor((state.currentCharacter)[''][0] * 0.1 + (state.currentCharacter)[''][0] * 0.25 + 25)
        },
        critic: (state) => {
            return Math.floor((state.currentCharacter)[''][0] * 0.1 + (state.currentCharacter)[''][0] * 0.05
                + (state.currentCharacter)[''][0] * 0.04 + 5)
        },
        movement: (state) => {
            return Math.floor((state.currentCharacter)[''][0] * 0.01)
        },
        growth: (state) => {
            return Math.floor((state.currentCharacter)[''][0] * 0.06 + 70)
        },
        healthRege: (state) => {
            const factor1 = Math.pow((state.currentCharacter)[''][0], 2) / ((state.currentCharacter)[''][0] + 5000)
            const factor2 = Math.pow((state.currentCharacter)[''][0], 2) / ((state.currentCharacter)[''][0] + 3000)
            const factor3 = (state.currentCharacter)[''][0] === 0 ? 0 : (state.currentCharacter)[''][1] / (state.currentCharacter)[''][0]
            return Math.floor((factor1 + factor2) * (1 + factor3))
        },
        manaRege: (state) => {
            const factor1 = (state.currentCharacter)[''][0] * 0.04
            const factor2 = (state.currentCharacter)[''][0] * 0.02
            const factor3 = (state.currentCharacter)[''][0] === 0 ? 0 : (state.currentCharacter)[''][1] / (state.currentCharacter)[''][0]
            return Math.floor((factor1 + factor2) * (1 + factor3))
        },
        characterBuilderValidation: (state) => {
            // 0 = 1 = 2 = 
            const warningLog = []
            if ((state.currentCharacter)[''] === '') {
                warningLog.push(['', 0])
            }
            if ((state.currentCharacter)[''] === '') {
                warningLog.push(['', 0])
            }
            if ((state.currentCharacter)[''] === '') {
                warningLog.push(['', 0])
            }
            if ((state.currentCharacter)[''] === '') {
                warningLog.push(['', 0])
            } else {
                if ((state.currentCharacter)[''] !== '' && (state.currentCharacter)[''] !== '') {
                    if (!(raceInstructions[(state.currentCharacter)['']][''].includes((state.currentCharacter)['']))) {
                        warningLog.push(['', 0])
                    }
                    if ((state.currentCharacter)[''] < raceInstructions[(state.currentCharacter)['']][''][0]) {
                        warningLog.push(['', 0])
                    } else if ((state.currentCharacter)[''] > raceInstructions[(state.currentCharacter)['']][''][1]) {
                        warningLog.push(['', 0])
                    }
                }
            }
            if ((state.currentCharacter)[''] === 0) {
                warningLog.push(['0', 1])
            }
            if ((state.currentCharacter)[''] === '') {
                warningLog.push(['', 0])
            } else if ((state.currentCharacter)[''] !== '') {
                if (['', '', ''].includes((state.currentCharacter)[''])) {
                    warningLog.push(['GM', 1])
                } else if ([''].includes((state.currentCharacter)[''])) {
                    warningLog.push(['GM', 1])
                }
                ['', '', '', '', '', '', '', ''].forEach(label => {
                    if ((state.currentCharacter)[label][0] < raceInstructions[(state.currentCharacter)['']][`${label}`][0]) {
                        warningLog.push([`${label}`, 0])
                    } else if ((state.currentCharacter)[label][0] > raceInstructions[(state.currentCharacter)['']][`${label}`][1]) {
                        warningLog.push([`${label}`, 0])
                    }
                })
            }
            if ((state.currentCharacter)[''] === '') {
                warningLog.push(['GMGM', 1])
            }
            if (state.statePoints > state.maxStatePoints) {
                warningLog.push(['', 0])
            } else if (state.statePoints < state.maxStatePoints - 1) {
                warningLog.push(['GM', 1])
            }
            ['', '', '', '', '', '', '', ''].forEach(label => {
                if (state.abilityPoints(label) < state.usedStateAbilityPoints(label)) {
                    warningLog.push([`${label}${label}`, 0])
                }
            })
            if (state.generalStatePoint < state.usedStateAbilityPoints('')) {
                warningLog.push(['', 0])
            } else if (state.generalStatePoint - 1000 > state.usedStateAbilityPoints('')) {
                warningLog.push(['1000', 1])
            }
            if (state.comprehensionPoints > state.level * 3 + 5 + (state.currentCharacter)['']) {
                warningLog.push(['', 0])
            } else if (state.comprehensionPoints < state.level * 3 + 5 + (state.currentCharacter)[''] - 20) {
                warningLog.push(['20', 1])
            }
            if (warningLog.length === 0) {
                warningLog.push(['', 2])
            }
            return warningLog
        },
        characterBuilderDownloadable: (state) => {
            const validationResult = state.characterBuilderValidation
            let downloadable = true
            for (let i = 0; i < validationResult.length; i++) {
                if (validationResult[i][1] === 0) {
                    downloadable = false
                    break
                }
            }
            return downloadable
        },
    },
    actions: {
        setAlert(title, content) {
            this.windowAlert = true
            this.windowAlertTitle = title
            this.windowAlertContent = content
        },
        setCharacter(character) {
            const previousVersion = character['']
            try {
                const vi = getVersionInitialisation()
                const newCharacter = {}
                Object.entries(vi).forEach(([key, value]) => {
                    if (key === '') {
                        newCharacter[key] = value
                    } else {
                        newCharacter[key] = character[key] ?? value
                    }
                })
                this.currentCharacter = newCharacter
                const thisVersion = newCharacter['']
                if (previousVersion) {
                    if (previousVersion === thisVersion) {
                        this.setAlert('', [''])
                    } else {
                        this.setAlert('', [`v${previousVersion}`, `v${thisVersion}`,
                            '',
                            ''])
                    }
                }
            } catch {
                this.setAlert('', ['JSON'])
            }
        },
        saveCharacter() {
            try {
                const jsonStr = JSON.stringify(this.currentCharacter, null, 2)
                const blob = new Blob([jsonStr], { type: "application/json" })
                const url = URL.createObjectURL(blob)
                const a = document.createElement("a")
                a.href = url
                a.download = `CGC-${(this.currentCharacter)['']}(v${(this.currentCharacter)['']}).json`
                document.body.appendChild(a)
                a.click()
                document.body.removeChild(a)
                URL.revokeObjectURL(url)
            } catch {
                this.setAlert('', ['', 'Amo'])
            }
        }
    },
})