<template>
    <div v-if="content"
        class="h-full w-full flex justify-center items-center bg-[rgb(230,230,180)] select-none break-all">
        <div class="fixed z-[1000] border-4 border-[rgb(130,130,80)] rounded-3xl bg-[rgba(200,200,150,0.8)] flex flex-row items-center"
            :style="{
                width: isMobile ? '95%' : '80%',
                height: isMobile ? '85%' : '80%',
            }">
            <!--  -->
            <div class="h-5/6 flex justify-center items-center" :style="{
                width: isMobile ? '30%' : '20%',
            }">
                <div class="h-full flex flex-col" :style="{
                    width: isMobile ? '90%' : '83.3%',
                    fontSize: isMobile ? '12px' : '16px',
                }">
                    <div v-for="(label, index) in labelsArray" :key="index"
                        class="w-full h-[12.5%] flex justify-center items-center">
                        <div class="click-button cursor-pointer w-full" @click="() => handleButtonClick(label)" :style="{
                            backgroundImage: content[0] === label ?
                                'linear-gradient(to bottom, rgba(180, 180, 130, 1.0), rgba(255, 255, 200, 0.5))' :
                                'linear-gradient(to bottom, rgba(255, 255, 200, 0.5), rgba(180, 180, 130, 1.0))',
                        }">
                            {{ label }}
                        </div>
                    </div>
                </div>
            </div>
            <!--  -->
            <div class="h-5/6 flex flex-row justify-center items-center border-l border-[rgb(130,130,80)]" :style="{
                width: isMobile ? '70%' : '80%',
            }">
                <div
                    class="w-[95%] h-full flex justify-center items-center flex-col border-2 border-[rgb(130,130,80)] rounded bg-[rgba(230,230,180,0.5)]">
                    <!--  -->
                    <div class="w-[95%] h-1/6 flex justify-center items-center border-b border-[rgb(130,130,80)] text-[rgb(60,60,60)] font-bold"
                        :style="{
                            fontSize: isMobile ? '20px' : '24px',
                        }">
                        {{ content[0] }}
                    </div>
                    <!--  -->
                    <div v-if="content[0] !== 'CGC'" ref="contentRef" class="w-[95%] h-5/6 overflow-y-auto" :style="{
                        fontSize: isMobile ? '14px' : '20px',
                    }">
                        <div v-for="([key, value], index) in Object.entries(content[1])" :key="index">
                            <div v-if="key === 'head'">
                                <div class="flex flex-col">
                                    <div v-for="(line, index2) in value" :key="index + '-' + index2" class="pt-2 pb-2">
                                        {{ line }}
                                    </div>
                                </div>
                            </div>
                            <div v-else class="pt-2 pb-2">
                                <span class="font-bold text-[rgb(60,60,60)]" :style="{
                                    fontSize: isMobile ? '14px' : '20px',
                                }">{{ key }}</span>{{ value }}
                            </div>
                        </div>
                    </div>
                    <div v-else class="w-[95%] h-5/6 overflow-y-auto flex flex-col items-center">
                        <div v-for="([routeName, routePath], index) in Object.entries(childPageRouter)" :key="index"
                            class="w-[95%] cursor-pointer flex justify-center items-center page-button" :style="{
                                paddingTop: isMobile ? '8px' : '16px',
                                paddingBottom: isMobile ? '8px' : '16px',
                                marginTop: isMobile ? '8px' : '16px',
                                marginBottom: isMobile ? '8px' : '16px',
                                fontSize: isMobile ? '14px' : '20px',
                            }" @click="handleDirectTo(routePath)">
                            {{ routeName }}
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <div class="fixed z-[999] w-full h-full" :style="{
            backgroundImage: isMobile ? 'url(background%20image/MainPageBackgroundMobile.jpg)' : 'url(background%20image/MainPageBackground.jpg)',
            backgroundSize: '100% 100%',
            opacity: '0.5',
        }"></div>
    </div>
</template>
<script setup>
import { onBeforeUnmount, onMounted, ref } from 'vue'
import router from '@/router'
import { versionTemplate } from '@/data/versionTemplate'
const content = ref(null)
const contentRef = ref(null)
const childPageRouter = {
    '': '/world_view',
    '': '/rules',
    '': '/character_builder',
    '': '/bap_calculator',
    '': '/attached_table',
    '': '/behind_the_scene',
}
const handleDirectTo = (routePath) => {
    const url = router.resolve(routePath).href
    window.open(url, '_blank')
}
const labelsArray = ['CGC', 'CGC', 'CGC', 'CGC', 'CGC', '', 'CGC', '']
const instructions = {
    'CGC': {
        'head': [
            'CGCChronicles of Grand Cosmos',
            '',
            '',
        ]
    },
    'CGC': {
        '': '15',
        '': '10',
        '': '',
        '': '',
        'NPC': '100NPCNPC',
        '': 'CGC3000',
    },
    'CGC': {
        '': 'CGCNPC',
        '': '',
        '': 'CGC',
        '': 'CGC',
        '': 'CGCNPC',
        '': 'CGCGMGM',
    },
    'CGC': {
        '': '',
        '': '',
        '': '',
        '': '',
        '': '',
        '': '',
        '': 'NPC',
        '': '',
        '': '',
        '': '',
        '': 'CGC',
    },
    'CGC': {
        'head': [''],
        '': 'CGC',
        '': 'QQQQ',
        '': '',
        '': '',
        '': '',
    },
    '': {
        'head': [
            'QQ462473152',
            'QQ4341446161',
            'amotyslee@gmail.com',
            'CGChttps://amotys.github.io/cgc-web/#/home',
        ]
    },
    '': {
        '': 'v' + versionTemplate.currentVersion,
        'CGC': 'Amotys',
        '': 'Amotys',
        'GM': 'Amotys1',
        'GM': 'deather1',
    },
}
const handleButtonClick = (label) => {
    content.value = [label, instructions[label]]
    if (contentRef.value) {
        contentRef.value.scrollTop = 0
    }
}
const isMobile = ref(false)
const updateWindowSize = () => {
    isMobile.value = window.innerHeight > window.innerWidth
}
onMounted(() => {
    updateWindowSize()
    window.addEventListener('resize', updateWindowSize)
    handleButtonClick('CGC')
})
onBeforeUnmount(() => {
    window.removeEventListener('resize', updateWindowSize)
})
</script>
<style scoped>
.click-button {
    border: 2px solid rgb(130, 130, 80);
    border-radius: 4px;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 80%;
    font-weight: bold;
    color: rgb(60, 60, 60);
}

.click-button:hover {
    background-color: rgba(100, 100, 60, 0.5);
}

.page-button {
    border: 2px solid rgb(130, 130, 80);
    border-radius: 8px;
    font-weight: bold;
    color: rgb(60, 60, 60);
    background-image: linear-gradient(to bottom, rgba(255, 255, 200, 0.5), rgba(180, 180, 130, 1.0));
}

.page-button:hover {
    background-color: rgba(100, 100, 60, 0.5);
}

.page-button:active {
    background-image: linear-gradient(to bottom, rgba(180, 180, 130, 1.0), rgba(255, 255, 200, 0.5));
}
</style>